import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { ProductStockService } from 'src/app/service/product-stock.service';

@Component({
  selector: 'app-product-stock',
  templateUrl: './product-stock.component.html',
})
export class ProductStockComponent implements OnInit {

  PageTitle = "Product Stock"

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;


  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private productStockService: ProductStockService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['product_stock'];
    // });
    this.loadform = false;
    this.getObjects();
  }
  
  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.productStockService.getProductStockList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
 
}




